
@font-face {
    font-family: 'PFZH';
    src: url('common/fonts/PFZH.ttf');
    font-size: normal;
    font-weight: normal;
}
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: PFZH;
}
#home {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
html {
  font-size: 32px;
}
input,
textarea,
button {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
}
@media screen and (min-width: 310px) {
html {
    font-size: 31px;
}
}
@media screen and (min-width: 320px) {
html {
    font-size: 32px;
}
}
@media screen and (min-width: 360px) {
html {
    font-size: 36px;
}
}
@media screen and (min-width: 375px) {
html {
    font-size: 37.5px;
}
}
@media screen and (min-width: 410px) {
html {
    font-size: 41px;
}
}
@media screen and (min-width: 414px) {
html {
    font-size: 41.4px;
}
}



