.mint-cell-wrapper {
    padding: 0;
    background: none;
}

.mint-cell:last-child {
    background: none;
}

.mint-cell-value {
    width: 100%;
}

.mint-msgbox {
    width: 6.4rem;
    border-radius: 0.2rem;
}

.mint-msgbox-confirm {
    color: #ff8039;
}

.mint-msgbox-cancel {
    color: #666666;
    border-right: 1px solid #ddd;
}
.mint-msgbox-btns {
    height: 1.28rem;
    font-size: 0.426667rem;
}
.mint-msgbox-message {
    display: flex;
    justify-content: center;
    align-items: center;
}
.mint-msgbox-content {
    height: 2.26667rem;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.mint-msgbox-message {
    font-size: 0.426667rem;
    color: #333333;
}
.picker-item {
    text-overflow: inherit;
}
.mint-swipe-indicator {
    width: 0.2rem;
    background: #ffffff;
    border-radius: 0.1rem;
    opacity: .5;
}
.mint-swipe-indicator.is-active {
    width: 0.8rem;
    opacity: 1;
}
.video-player {
    height: 100%!important;
}
.vjs_video_978-dimensions,
.vjs_video_3-dimensions {
    height: 100%;
}
.video-js .vjs-big-play-button{
   height: 2em!important;
   width: 2em!important;
   line-height: 2em!important;
   border-radius: 1em!important;
 }
 .vjs-button {
   outline: none;
 }
 .mint-indicator {
    z-index: 9999;
 }
 .mint-indicator-wrapper {
    background: #ffffff;
    border-radius: 1px solid #f5f5f5;
 }
 .mint-indicator-text {
    color: #333333;
 }
.mint-switch-core {
    width: 0.97778rem;
    height: 0.58667rem;
}
.mint-switch-core::before {
    width: 0.97778rem;
    height: 0.58667rem;
}
.mint-switch-core::after {
    width: 0.58667rem;
    height: 0.58667rem;
}
.mint-switch-input:checked + .mint-switch-core {
    border: 1px solid #fff;
    background: #ff7e7e;
}


